.o-hero {
  background-color:    $green;
  background-position: center center;
  background-repeat:   no-repeat;
  background-size:     100% auto;
  color:               $white;
}

  @media only screen and (max-width: 56.24rem) {
    .o-hero {
      background-image: none !important;
    }
  }

  @include respond-to('regular') {
    .o-hero {
      @include rem('min-height', $hero-body-min-height);
    }
  }

  .o-hero--bg-top,
  .o-hero--bg-top .c-grid {
    background-position: center top;
  }

  .o-hero--bg-bottom,
  .o-hero--bg-bottom .c-grid {
    background-position: center bottom;
  }

  .o-hero__nav {
    @include padding($padding-x-small, 0);
  }

  .o-hero__nav-left {
    float: left;
  }

  .o-hero__nav-right {
    float: right;
  }

  .o-hero__nav-link {
    display: inline-block;
    color:   $white;
    @include margin(0, $margin-xx-small);
    @include rem('font-size', $font-size-small);
    text-decoration: none;

    &:hover,
    &:active,
    &:focus {
      color:           $white;
      text-decoration: underline;
    }
  }

  .o-hero__body {
    @include padding($padding-xxxx-large, 0);
    max-width:  $hero-body-max-width;
    margin:     0 auto;
    text-align: center;
  }

  .o-hero__title {
    &:last-child {
      margin: 0;
    }
  }

    .o-hero__title--sentence {
      @include h2;
    }

  .o-hero__subtitle {
    @include rem('font-size', $font-size-large);
  }

  .o-hero__list {
    display: inline-block;
    margin:  0;
    padding: 0;
  }

    .o-hero__item {
      float:      left;
      list-style: none;
      @include margin(0, $margin-small);
    }
